import React, {PropTypes} from 'react'
import ErrorDialog from '../components/ErrorDialog'
import request from '../util/request'
import LoadingContainer from './LoadingContainer'

// Loads the most basic data from a Sanity project
class VisionContainer extends LoadingContainer {
  getSubscriptions() {
    return [
      request(this, {uri: '/datasets'}, 'datasets')
    ]
  }

  render() {
    if (this.state.error) {
      return (
        <ErrorDialog
          heading="An error occured while loading project data"
          error={this.state.error}
        />
      )
    }

    return <code><pre>{JSON.stringify(this.state, null, 2)}</pre></code>
  }
}

VisionContainer.contextTypes = {
  client: PropTypes.shape({fetch: PropTypes.func}).isRequired
}

export default VisionContainer



