import React from 'react'
import ReactDOM from 'react-dom'
import createClient from '@sanity/client'
import SanityVision from '../../src/Vision'
import Header from './Header'

class SanityVisionDemo extends React.Component {
  constructor() {
    super()

    this.handleProjectChange = this.handleProjectChange.bind(this)
  }

  handleProjectChange(event) {
    console.log(event.target)
  }

  shouldComponentUpdate() {
    return false
  }

  render() {
    const client = createClient({projectId: 's2w5wbtl'})
    return (
      <form action="#" onSubmit={this.handleProjectChange} className="pure-form">
        <Header />

        <div className="vision">
          <SanityVision client={client} />
        </div>
      </form>
    )
  }
}

ReactDOM.render(
  <SanityVisionDemo />,
  document.getElementById('root')
)



